import React, { useState, useEffect } from "react";
import CryptoJS from "crypto-js";
import {
  Button,
  Card,
  CardContent,
  Typography,
  Box,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
} from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import HighlightOffIcon from "@mui/icons-material/HighlightOff";

function Quiz() {
  const [questions, setQuestions] = useState([]);
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [score, setScore] = useState(0);
  const [showScore, setShowScore] = useState(false);
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [feedback, setFeedback] = useState(null);
  const [selectedPart, setSelectedPart] = useState("");
  const [loading, setLoading] = useState(false);
  const [highlightCorrectAnswer, setHighlightCorrectAnswer] = useState(false);

  // Function to load and decrypt questions based on selected part
  const loadQuestions = async (part) => {
    setLoading(true);
    try {
      // Dynamically require the encrypted file based on selected part
      const encryptedData = await import(`./encryptedQuestions${part}.json`);
      const secretKey = "your-secret-key"; // Use the same key as in your encryption
      const decryptedData = CryptoJS.AES.decrypt(
        encryptedData.data,
        secretKey
      ).toString(CryptoJS.enc.Utf8);
      setQuestions(JSON.parse(decryptedData));
      setCurrentQuestion(0); // Reset to the first question
      setScore(0); // Reset score
      setShowScore(false); // Hide score until the end
      setHighlightCorrectAnswer(false); // Reset the highlight when loading a new part
    } catch (error) {
      console.error("Error loading questions:", error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (selectedPart) {
      loadQuestions(selectedPart);
    }
  }, [selectedPart]);

  useEffect(() => {
    // Reset the highlight state whenever the questions change (e.g., when moving to a new part)
    setHighlightCorrectAnswer(false);
  }, [questions]);

  if (loading) {
    return <div>Loading questions...</div>;
  }

  if (!selectedPart) {
    return (
      <Box
        display="flex"
        justifyContent="center"
        alignItems="center"
        minHeight="100vh"
        bgcolor="#f5f5f5"
      >
        <Card sx={{ width: 500, p: 3 }}>
          <CardContent>
            <Typography variant="h5" gutterBottom>
              Select a Questionnaire Part
            </Typography>
            <FormControl fullWidth>
              <InputLabel id="select-part-label">Select Part</InputLabel>
              <Select
                labelId="select-part-label"
                value={selectedPart}
                onChange={(e) => setSelectedPart(e.target.value)}
                label="Select Part"
              >
                <MenuItem value="1">Part 1</MenuItem>
                <MenuItem value="2">Part 2</MenuItem>
                <MenuItem value="3">Part 3</MenuItem>
                <MenuItem value="4">Part 4</MenuItem>
                <MenuItem value="5">Part 5</MenuItem>
                <MenuItem value="6">Part 6</MenuItem>
              </Select>
            </FormControl>
          </CardContent>
        </Card>
      </Box>
    );
  }

  if (questions.length === 0) {
    return <div>Loading questions...</div>;
  }

  const question = questions[currentQuestion];

  const handleAnswerClick = (option) => {
    setSelectedAnswer(option);

    if (option === question.answer) {
      setScore(score + 1);
      setFeedback("correct");
      setHighlightCorrectAnswer(false); // Reset the highlight after correct answer
    } else {
      setFeedback("incorrect");
      setHighlightCorrectAnswer(true); // Highlight correct answer in green when incorrect

      setTimeout(() => {
        setHighlightCorrectAnswer(false); // Reset highlight after 5 seconds
      }, 5000);
    }

    setTimeout(() => {
      setFeedback(null);
      setSelectedAnswer(null);
      const nextQuestion = currentQuestion + 1;
      if (nextQuestion < questions.length) {
        setCurrentQuestion(nextQuestion);
        setHighlightCorrectAnswer(false); // Reset highlight when moving to next question
      } else {
        setShowScore(true);
      }
    }, 1000);
  };

  // Handle Back button logic
  const handleBack = () => {
    if (currentQuestion > 0) {
      setCurrentQuestion(currentQuestion - 1); // Go back to the previous question
    }
  };

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      minHeight="100vh"
      bgcolor="#f5f5f5"
    >
      <Card sx={{ width: 500, p: 3 }}>
        <CardContent>
          {showScore ? (
            <Typography variant="h5" align="center">
              You scored {score} out of {questions.length}
            </Typography>
          ) : (
            <>
              <Typography variant="h6" gutterBottom>
                Question {currentQuestion + 1}/{questions.length}
              </Typography>
              <Typography variant="h5" gutterBottom>
                {question.question}
                {question.code ? (
                  question.code.split("\n").map((line, index) => (
                    <React.Fragment key={index}>
                      {line}
                      <br />
                    </React.Fragment>
                  ))
                ) : (
                  <span></span> // Or leave this part empty if you prefer
                )}
              </Typography>

              <Box mt={2}>
                {Object.entries(question.options).map(([key, value]) => (
                  <Button
                    key={key}
                    variant="contained"
                    color={
                      selectedAnswer === key
                        ? feedback === "correct"
                          ? "success"
                          : "error"
                        : "primary"
                    }
                    onClick={() => handleAnswerClick(key)}
                    sx={{
                      display: "block",
                      width: "100%",
                      my: 1, // Margin between buttons (vertical gap)
                      textTransform: "none", // Ensure no uppercase text
                      backgroundColor:
                        highlightCorrectAnswer && key === question.answer
                          ? "green"
                          : "", // Highlight correct answer in green
                    }}
                    startIcon={
                      selectedAnswer === key &&
                      (feedback === "correct" ? (
                        <CheckCircleIcon />
                      ) : (
                        <HighlightOffIcon />
                      ))
                    }
                  >
                    {value}
                  </Button>
                ))}
              </Box>

              <Box display="flex" justifyContent="space-between" mt={2}>
                {/* Back Button */}
                <Button
                  variant="outlined"
                  onClick={handleBack}
                  disabled={currentQuestion === 0}
                >
                  Back
                </Button>

                {/* Next Question Button */}
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    const nextQuestion = currentQuestion + 1;
                    if (nextQuestion < questions.length) {
                      setCurrentQuestion(nextQuestion);
                    } else {
                      setShowScore(true);
                    }
                  }}
                >
                  Next Question
                </Button>
              </Box>
            </>
          )}
        </CardContent>
      </Card>
    </Box>
  );
}

export default Quiz;
